import React from 'react';
import { useConditionalField } from './conditionalForm';
import { RadioInput, RadioInputProps } from './radioInput';
import { useFieldError } from './useFieldError';
import { useMemoizedField } from './useMemoizedField';

export type RadioFieldProps<T = any> = Omit<
  RadioInputProps<T>,
  'name' | 'value' | 'onChange' | 'error'
> & {
  /**
   * Field name
   */
  readonly name: string;
};

/**
 * Material-UI Radio with Formik support.
 */
export function RadioField<T>(props: RadioFieldProps<T>) {
  const { name } = props;

  useConditionalField(props.name, 'RadioField');

  const [field, , { setValue, setTouched }] = useMemoizedField(name);
  const error = useFieldError(name);

  return (
    <RadioInput
      {...props}
      value={field.value}
      onChange={value => {
        setTouched(true);
        setValue(value);
      }}
      error={error}
    />
  );
}
